import React, { useState, useEffect, useRef } from 'react';
import Switch from '@material-ui/core/Switch';
import { IconButton } from '@material-ui/core';
import FlipCameraIosIcon from '@material-ui/icons/FlipCameraIos';
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CircularProgress from '@material-ui/core/CircularProgress';
import Webcam from 'react-webcam';
import '@tensorflow/tfjs-backend-cpu';
import * as cocoSsd from '@tensorflow-models/coco-ssd';

const UseWindowDimensions = () => {
    const getWindowDimensions = () => {
        const { innerWidth: width, innerHeight: height } = window;
        return { width, height }
    }
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
    useEffect(() => {
        const onResize = () => {
            setWindowDimensions(getWindowDimensions());
        }
        window.addEventListener('resize', onResize);
        return () => window.removeEventListener('resize', onResize);
    }, []);
    return windowDimensions;
}

interface ButtonsProps {
    isCameraEnable: boolean
    setCameraEnable: Function
    isCaptured: boolean
    setCaptured: Function
    shutter: boolean
    setShutter: Function
    isDetected: boolean
    setDetected: Function
    facingMode: string
    setFacingMode: Function
    isBackgroundEnable: boolean
    setBackgroundEnable: Function
    processing: boolean
}
const Buttons: React.FC<ButtonsProps> = ({
    isCameraEnable,
    setCameraEnable,
    isCaptured,
    setCaptured,
    shutter,
    setShutter,
    isDetected,
    setDetected,
    facingMode,
    setFacingMode,
    isBackgroundEnable,
    setBackgroundEnable,
    processing
}) => {
    return (
        <div
            style={{
                display: 'flex',
                justifyContent: 'space-around',
                alignItems: 'center'
            }}
        >
            {/* camera start or capture button */}
            <IconButton disabled={processing}>
                {isCaptured ||
                    <CameraAltIcon color={processing ? 'disabled' : 'primary'} onClick={() => setShutter(true)}/>
                }
                {isCaptured &&
                    <CameraAltIcon 
                        color={processing ? 'disabled' : 'primary'} 
                        onClick={() => {
                            setCaptured(false)
                            setDetected(false)
                        }}
                    />
                }
            </IconButton>

            {/* select camera */}
            <IconButton disabled={isCaptured || processing}>
                {(isCaptured) && 
                    <FlipCameraIosIcon color='disabled'/>
                }
                {isCaptured ||
                    <>
                        {facingMode === 'user' &&
                            <FlipCameraIosIcon color={processing ? 'disabled' : 'primary'} onClick={() => setFacingMode('environment')}/>
                        }
                        {facingMode === 'environment' &&
                            <FlipCameraIosIcon color={processing ? 'disabled' : 'primary'} onClick={() => setFacingMode('user')}/>
                        }
                    </>
                }
            </IconButton>

            {/* background */}
            <Switch defaultChecked color='primary' disabled={!isCaptured} onChange={() => setBackgroundEnable(!isBackgroundEnable)} />

            {/* upload */}
            <IconButton disabled={true}>
                <CloudUploadIcon color='disabled'/>
            </IconButton>
        </div>
    );
}

interface CanvasProps {
    webcamRef: any
    facingMode: string
    width: number
    height: number
    isCaptured: boolean
    setCaptured: Function
    isBackgroundEnable: boolean
    shutter: boolean
    setShutter: Function
    isDetected: boolean
    setDetected: Function
    processing: boolean
    setProcessing: Function
}

const Canvas: React.FC<CanvasProps> = ({
    webcamRef,
    facingMode,
    width,
    height,
    isCaptured,
    setCaptured,
    isBackgroundEnable,
    shutter,
    setShutter,
    isDetected,
    setDetected,
    processing,
    setProcessing
}) => {
    const screenshotRef = useRef(true);
    const drawCanvasRef = useRef(true);
    const detectRef = useRef(true);
    const [model, setModel] = useState<any>(null);
    const [predictions, setPredictions] = useState<any>(null);
    const [screenshot, setScreenshot] = useState<HTMLImageElement>(new Image);
    const canvasRef = useRef<HTMLCanvasElement>(null);

    const GetScreenshot = async () => {
        console.log('start screenshot.');
        if (webcamRef.current) {
            const webcamCurrent = webcamRef.current as any;
            console.log('webcam ready.')
            if (webcamCurrent.video.readyState === 4) {
                const imgSrc = webcamRef.current?.getScreenshot();
                let img = new Image;
                img.src = imgSrc;
                img.onload = () => {
                    console.log('end screenshot.');
                    setScreenshot(img);
                    setShutter(false);
                    setCaptured(true);
                }
            }
        }
    }
    const Detect = async () => {
        if (isCaptured) {
            console.log('detect start.');
            setProcessing(true);
            const preds = await model.detect(screenshot);
            console.log('detect end.');
            if (preds.length) {
                console.log(predictions);
                setPredictions(preds);
            }
            setProcessing(false);
            setDetected(true);
        }
    }
    const DrawCanvas = async () => {
        if (isCaptured) {
            if (canvasRef.current) {
                console.log('canvas ready.');
                canvasRef.current.width = screenshot.naturalWidth;
                canvasRef.current.height = screenshot.naturalHeight;
                const context = canvasRef.current.getContext('2d');
                if (isBackgroundEnable) {
                    context?.drawImage(screenshot, 0, 0);
                }
                predictions.forEach((prediction: any) => {
                    if (prediction.class === 'person' && prediction.score >= 0.5) {
                        const bbox = prediction.bbox;
                        context?.strokeRect(bbox[0], bbox[1], bbox[2], bbox[3]);
                    }
                })
                console.log('drawn.');
            }
        }
        /*
        setProcessing(true);
        if (isCaptured) {
            if (webcamRef.current && canvasRef.current) {
                console.log('webcam and canvas are ready.');
                const webcamCurrent = webcamRef.current as any;
                const ctx = canvasRef.current.getContext('2d');
                await setTimeout(() => {
                    console.log('wait')
                    console.log(webcamCurrent.video.readyState);
                    if (webcamCurrent.video.readyState === 4) {
                        console.log('webcam current ready state is 4.');
                        const imgSrc = webcamRef.current?.getScreenshot();
                        let img = new Image;
                        img.src = imgSrc;
                        console.log(img);
                        ctx?.drawImage(img, 0, 0);

                        setProcessing(false);
                    }
                }, 100);
            }
        }
        */
    }
    const LoadModel = async () => {
        console.log('start model loading.');
        setProcessing(true);
        const cocossd = await cocoSsd.load();
        setModel(cocossd);
        setProcessing(false);
        console.log('model loaded.')
    }
    useEffect(() => {
        LoadModel();
    }, []);

    useEffect(() => {
        if (screenshotRef.current) {
            screenshotRef.current = false;
            return;
        }
        GetScreenshot();
    }, [shutter]);

    useEffect(() => {
        if (drawCanvasRef.current) {
            drawCanvasRef.current = false;
            return;
        }
        Detect();
    }, [isCaptured])

    useEffect(() => {
        if (detectRef.current) {
            detectRef.current = false;
            return;
        }
        DrawCanvas();
    }, [isDetected, isBackgroundEnable])

    console.log('shutter', shutter, 'processing', processing, 'isCaptured', isCaptured, 'condition', (isCaptured && !processing));
    return (
        <div 
            style={{
                width: width, 
                height: height*0.9,
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
            }}
        >
            {(!isCaptured && !processing) &&
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    videoConstraints={{
                        facingMode: facingMode
                    }}
                    style={{
                        margin: 'auto',
                        textAlign: 'center',
                        zIndex: 9,
                        width: '100%',
                        height: '100%'
                    }}
                />
            }
            {processing && 
                <CircularProgress 
                />
            }
            {(isCaptured && !processing) && 
                <>
                    <canvas
                        ref={canvasRef}
                        style={{
                            margin: 'auto',
                            width: '100%',
                            height: '100%',
                            zIndex: 30
                        }}
                    />
                </>
            }
        </div>
    );
}

export const ObjectDetector: React.FC = () => {
    const [isCameraEnable, setCameraEnable] = useState<boolean>(true);
    const [isCaptured, setCaptured] = useState<boolean>(false);
    const [shutter, setShutter] = useState<boolean>(false);
    const [isDetected, setDetected] = useState<boolean>(false);
    const [isBackgroundEnable, setBackgroudEnable] = useState<boolean>(true);
    const [facingMode, setFacingMode] = useState<string>('user');
    const [processing, setProcessing] = useState<boolean>(false);
    const { width, height } = UseWindowDimensions();
    const webcamRef = useRef<Webcam>(null);
    return (
        <div>
            <Buttons
                isCameraEnable={isCameraEnable}
                setCameraEnable={setCameraEnable}
                isCaptured={isCaptured}
                setCaptured={setCaptured}
                shutter={shutter}
                setShutter={setShutter}
                isDetected={isDetected}
                setDetected={setDetected}
                facingMode={facingMode}
                setFacingMode={setFacingMode}
                isBackgroundEnable={isBackgroundEnable}
                setBackgroundEnable={setBackgroudEnable}
                processing={processing}
            />
            <Canvas
                webcamRef={webcamRef}
                facingMode={facingMode}
                width={width}
                height={height}
                isCaptured={isCaptured}
                setCaptured={setCaptured}
                isBackgroundEnable={isBackgroundEnable}
                shutter={shutter}
                setShutter={setShutter}
                isDetected={isDetected}
                setDetected={setDetected}
                processing={processing}
                setProcessing={setProcessing}
            />
        </div>
    );
}